* {
  margin: 0;
  padding: 0;
}

body {
  max-width: 1920px;
  border: 7px solid #1f3d5f;
  border-radius: 10px;
  margin: 15px auto;
}

img {
  width: 320px;
  height: 200px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 15px;
  transition-duration: .5s;
}

p {
  letter-spacing: 1.3px;
  color: #fff;
  text-align: center;
  font-family: Courier New, Courier, monospace;
  font-size: 12px;
}

.search-form {
  padding: 20px 0;
}

.gallery {
  background-color: #3675db;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 20px 0;
  display: flex;
}

.info {
  flex-direction: row;
  gap: 5px;
  display: flex;
}

.search-form-wrapper {
  background-color: #3675db;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form :-ms-placeholder-shown {
  text-align: center;
  text-transform: none;
  background-color: #d3d3d3;
}

.search-form :placeholder-shown {
  text-align: center;
  text-transform: none;
  background-color: #d3d3d3;
}

input[type="text"] {
  text-align: center;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  color: #17524a;
  margin: 0 auto;
  padding: 0;
}

.search-btn {
  margin-left: 15px;
  padding: 0 15px;
}

.load-more-wrapper {
  background-color: #3675db;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 30px;
  display: flex;
}

.load-more-btn {
  cursor: pointer;
  padding: 5px 20px;
}

.photo-card {
  width: 320px;
  cursor: pointer;
  background-color: #a9c0f9;
  border: 2px solid rgba(0, 0, 0, .3);
  border-radius: 10px;
  padding: 10px;
  transition-duration: .5s;
  box-shadow: 2px 2px 20px #91aae4;
}

.photo-card:hover {
  transform-origin: 0;
  border-bottom-right-radius: 10%;
  transform: scaleX(1.03)rotateY(10deg)skewY(-4deg);
  box-shadow: 50px 10px 10px rgba(0, 0, 0, .2);
}

.sl-image {
  width: 10%;
  background-color: #a9c0f9;
  border: 15px solid #282456;
  border-radius: 10px;
  padding: 10px;
  transition-duration: 1s;
  box-shadow: 5px 5px 20px #91aae4;
}

img:hover {
  transform-origin: 0;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-left: 10px;
  transform: scale(1.1)rotateY(-10deg)skewY(4deg);
  box-shadow: 20px 20px 15px rgba(0, 0, 0, .5);
}

